import Vue from 'vue'

import Router from 'vue-router'
import Index from '@/pages/Index'
import Content from '@/pages/Content'
import Search from '@/pages/Search'
import Move from '@/pages/Move'
import Item from '@/pages/Item'
import Pgl from '@/pages/Pgl'


Vue.use(Router)
 
export default new Router({
　　routes: [
　　　　{
　　　　　　path: '/category/',
　　　　　　component: Index
　　　　},
　　　　{
　　　　　　path: '/category/:id/',
　　　　　　component: Index
　　　　},
       {
　　　　　　path: '/pokemon/:id/',
　　　　　　component: Content
　　　　},
        {
　　　　　　path: '/pokemon/',
　　　　　　component: Search
　　　　},
        {
　　　　　　path: '/move',
　　　　　　component: Move
　　　　},
        {
　　　　　　path: '/item',
　　　　　　component: Item
　　　　},
        {
　　　　　　path: '/pgl/:season/',
　　　　　　component: Pgl
　　　　},
        {
　　　　　　path: '/pgl/',
　　　　　　component: Pgl
　　　　},

　　]
})


