<template>
    <div class="row clearfix">
        <div class="col-md-12 column">
            <b-navbar toggleable="md" type="dark" variant="dark">
                <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
                <b-navbar-brand href="#">Pokemon</b-navbar-brand>
                <b-collapse is-nav id="nav_collapse">
                    <b-navbar-nav>
                        <b-nav-item :to="'/category/1'">G1</b-nav-item>
                        <b-nav-item :to="'/category/2'">G2</b-nav-item>
                        <b-nav-item :to="'/category/3'">G3</b-nav-item>
                        <b-nav-item :to="'/category/4'">G4</b-nav-item>
                        <b-nav-item :to="'/category/5'">G5</b-nav-item>
                        <b-nav-item :to="'/category/6'">G6</b-nav-item>
                        <b-nav-item :to="'/category/7'">G7</b-nav-item>
                    </b-navbar-nav>
                    <b-nav-form @submit.prevent="callmethod">
                        <b-form-input size="sm" class="mr-sm-1" type="text" placeholder="Search Pokemon" v-model="searchtext"/>
                        <b-button size="sm" class="my-2 my-sm-1" type="submit">Search</b-button>
                    </b-nav-form>
                    <b-navbar-nav class="ml-auto">
                    <b-nav-item :to="'/move'">Move</b-nav-item>
                    <b-nav-item :to="'/item'">Item</b-nav-item>
                    <b-nav-item-dropdown text="PGLRank" right>
                        <b-dropdown-item :to="'/pgl/310'">Season 310</b-dropdown-item>
                        <b-dropdown-item :to="'/pgl/309'">Season 309</b-dropdown-item>
                        <b-dropdown-item :to="'/pgl/308'">Season 308</b-dropdown-item>
                    </b-nav-item-dropdown>
                    </b-navbar-nav>
                </b-collapse>
            </b-navbar>
            <hr />

        </div>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                Gid: this.$route.params.id,
                searchtext: '',
            }
        },
        watch: { //router-link同页面路由跳转，监听router变化
            '$route' (to, from) {
                this.Gid = this.$route.params.id
            }
        },
        computed: {},
        created () {},
        methods: {
            callmethod(){
                this.$router.push({path:'/pokemon/', query:{search: this.searchtext}})
            },
        }
    }
</script>


