<template>
<div class="container">
    
    <Header ref="child" v-on:childMethod="search"></Header>
    <div class="row clearfix">
		<div class="col-md-6 column">
			<div class="jumbotron" style="height:580px;">
				<h2>
					The Pokemons!
				</h2>
				<p>
					This is a template for a simple marketing or informational website. It includes a large callout called the hero unit and three supporting pieces of content. Use it as a starting point to create something more unique.
				</p>
				<p>
					 <button class="btn btn-primary btn-large" href="#">Learn more</button>
				</p>
			</div>
		</div>
		<div class="col-md-6 column">
			<div class="card">
				<div class="card-header">
					<h3 class="card-text" style="font-size:12pt">
						搜索到 {{count}} 个结果
					</h3>
				</div>
				<div class="card-body" style="overflow-y:scroll;height:500px;padding: 3px 3px 3px 3px">
					<table v-if="count !== 0" class="table table-hover table-striped table-sm table-responsive" style="font-size:11pt">
                        <thead>
                            <tr>
                                <th>全国编号</th>
                                <th></th>
                                <th>名字</th>
                                <th>别名</th>
                                <th>属性</th>
                                <th>英文</th>
                                <th>世代</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="poke in results" :key="poke.id">
                                <td>{{poke.num}}</td>
                                <td><img v-bind:src="'/static/img/icon/'+poke.pic+'.png'" /></td>
                                <td><router-link :to="'/pokemon/'+poke.id">{{poke.name}}</router-link></td>
                                <td>{{poke.name1}}</td>
                                <td>
                                    <span class="badge badge-secondary" v-bind:style="{'background-color':poke.type1.color}">{{poke.type1.name}}</span> <span v-if="poke.type2.name !== '-'" class="badge badge-secondary" v-bind:style="{'background-color':poke.type2.color}">{{poke.type2.name}}</span>
                                </td>
                                <td>{{poke.name_en}}</td>
                                <td>G{{poke.category}}</td>
                            </tr>
                        </tbody>
                    </table>
				</div>
				<div class="card-footer">
					Panel footer
				</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
    import Header from '../components/header.vue'
    export default {
        components: {Header},
        data () {
            return {
                results: {},
                count: 0,
            }
        },
        watch: {
            '$route' (to, from) {
                this.id = this.$route.params.id
                this.search()
            }
        },
        computed: {

        },
        created () {
            this.search()
        },
        methods: {
            search: function(){
                let v = this
                v.$api.get('pokemon/', this.$route.query, function(r){
                    v.results = r.results
                    v.count = r.count
                })
            }
        }
    }
</script>


