<template>
    <div class="container">
        <Header></Header>
        <div class="row clearfix">
            <div class="col-md-4 column">
                <div class="card bg-light">
                    <div class="card-header" style="height: 60px">
                        <div style="float:left;"><h6><b># {{pokemon.num}} {{pokemon.name}}</b></h6></div>
                        <div style="float:right;"><h6><b>{{pokemon.name1}}</b></h6></div>
                    </div>
                    <div class="card-body" style="padding: 0px 0px 0px 0px;">
                        <div class="row">
                        
                        <div class="col-md-8">
                            <img alt="wiki" height="200" width="200" v-bind:src="'/static/img/wiki/'+pokemon.pic+'.png'" class="img-thumbnail" />
                        </div>
                        <div class="col-md-4">
                            <img alt="gif" style="max-height:92px;max-width:109px;" v-bind:src="'/static/img/front/'+pokemon.pic+'.gif'" class="img-thumbnail" />
                            <img alt="shiny-gif" style="max-height:92px;max-width:109px;" v-bind:src="'/static/img/front-shiny/'+pokemon.pic+'.gif'" class="img-thumbnail" />
                        </div>
                        </div>
                    </div>
                    <div class="card-footer" style="padding: 2px 2px 2px 2px;">
                        <div class="row">
                        <div class="col-6">
                        <h6><span class="badge badge-danger" v-bind:style="{'background-color':pokemon.type1.color}">{{pokemon.type1.name}}</span> <span v-if="pokemon.type2.name !== '-'" class="badge badge-danger" v-bind:style="{'background-color':pokemon.type2.color}">{{pokemon.type2.name}}</span></h6>
                        </div>

                        <div div="col-3"><router-link :to="'/pokemon/'+prev"><button class="btn btn-warning btn-sm">Prev</button></router-link></div>
                        <div class=col-3><router-link :to="'/pokemon/'+next"><button class="btn btn-success btn-sm">Next</button></router-link></div>
                        </div>
                    </div>
                    <div class="card-footer" style="padding: 0px 0px 0px 0px;">
                        <table class="table table-condensed" style="font-size: 10pt;margin: 0px 0px 0px 0px;">
                            <thead>
                                <tr>
                                    <th style="width:85px">一般特性</th>
                                    <th>说明</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{pokemon.ability1.name}}</td>
                                    <td>{{pokemon.ability1.detail}}</td>
                                </tr>
                                <tr>
                                    <td>{{pokemon.ability2.name}}</td>
                                    <td>{{pokemon.ability2.detail}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="card-footer" style="padding: 0px 0px 0px 0px;">
                        <table class="table table-condensed" style="font-size: 10pt;margin: 0px 0px 0px 0px;">
                            <thead>
                                <tr>
                                    <th style="width:85px">隐藏特性</th>
                                    <th>说明</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{pokemon.ability3.name}}</td>
                                    <td>{{pokemon.ability3.detail}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div class="col-md-8 column">
                <div class="card bg-light">
                    <div class="card-header">
                        <h6><b>能力值计算  {{ability}}</b></h6>
                    </div>
                <div class="card-body" style="height:500px;padding: 0px 0px 0px 0px;">
                    <div class="statform">
                        <div class="col labelcol">
                            <div></div>
                            <div><b>Hp</b></div>
                            <div><b>攻击</b></div>
                            <div><b>防御</b></div>
                            <div><b>特攻</b></div>
                            <div><b>特防</b></div>
                            <div><b>速度</b></div>
                        </div>
                        <div class="col basestatscol">
                            <div><b>Base</b></div>
                            <div><b>{{pokemon.hp}}</b></div>
                            <div><b>{{pokemon.atk}}</b></div>
                            <div><b>{{pokemon.defen}}</b></div>
                            <div><b>{{pokemon.satk}}</b></div>
                            <div><b>{{pokemon.sdef}}</b></div>
                            <div><b>{{pokemon.sp}}</b></div>
                        </div>
                        <div class="col graphcol">
                            <div></div>
                            <div v-if="pokemon.id === 335"><em><span style="background:rgb(100,100,0);width:1px"></span></em></div>
                            <div v-if="pokemon.id !== 335"><em><span v-bind:style="{background:'rgb(100,'+hp_state+',0)',width:hp_state*2/3+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+atk_state+',0)',width:atk_state+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+defen_state+',0)',width:defen_state+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+satk_state+',0)',width:satk_state+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+sdef_state+',0)',width:sdef_state+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+sp_state+',0)',width:sp_state+'px'}"></span></em></div>
                        </div>
                        <div class="col statscol">
                            <div></div>
                            <div v-if="pokemon.id === 335"><b>1</b></div>
                            <div v-if="pokemon.id !== 335"><b>{{hp_state}}</b></div>
                            <div><b>{{atk_state}}</b></div>
                            <div><b>{{defen_state}}</b></div>
                            <div><b>{{satk_state}}</b></div>
                            <div><b>{{sdef_state}}</b></div>
                            <div><b>{{sp_state}}</b></div>
                        </div>
                        <div class="col evcol">
                            <div><strong>EVs</strong></div>
                            <div><input class="textbox inputform numform" type="text" v-model="hpbase" placeholder="0-252" value="0" autoComplete='off'></div>
                            <div><input class="textbox inputform numform" type="text" v-model="atkbase" placeholder="0-252" value="0" autoComplete='off'></div>
                            <div><input class="textbox inputform numform" type="text" v-model="defenbase" placeholder="0-252" value="0" autoComplete='off'></div>
                            <div><input class="textbox inputform numform" type="text" v-model="satkbase" placeholder="0-252" value="0" autoComplete='off'></div>
                            <div><input class="textbox inputform numform" type="text" v-model="sdefbase" placeholder="0-252" value="0" autoComplete='off'></div>
                            <div><input class="textbox inputform numform" type="text" v-model="spbase" placeholder="0-252" value="0" autoComplete='off'></div>
                        </div>
                        <div class="col evslidercol">
                            <div></div>
                            <div><input type="range" class="evslider" v-model="hpex" min="0" max="252" step="1" value="0" aria-hidden="true"/></div>
                            <div><input type="range" class="evslider" v-model="atkex" min="0" max="252" step="1" value="0" aria-hidden="true"/></div>
                            <div><input type="range" class="evslider" v-model="defenex" min="0" max="252" step="1" value="0" aria-hidden="true"/></div>
                            <div><input type="range" class="evslider" v-model="satkex" min="0" max="252" step="1" value="0" aria-hidden="true"/></div>
                            <div><input type="range" class="evslider" v-model="sdefex" min="0" max="252" step="1" value="0" aria-hidden="true"/></div>
                            <div><input type="range" class="evslider" v-model="spex" min="0" max="252" step="1" value="0" aria-hidden="true"/></div>
                        </div>
                        <div class="col ivcol">
                            <div><strong>IVs</strong></div>
                            <div><input class="textbox inputform numform" type="number" v-model="hpiv" min="0" max="31" step="1" value="31"></div>
                            <div><input class="textbox inputform numform" type="number" v-model="atkiv" min="0" max="31" step="1" value="31"></div>
                            <div><input class="textbox inputform numform" type="number" v-model="defeniv" min="0" max="31" step="1" value="31"></div>
                            <div><input class="textbox inputform numform" type="number" v-model="satkiv" min="0" max="31" step="1" value="31"></div>
                            <div><input class="textbox inputform numform" type="number" v-model="sdefiv" min="0" max="31" step="1" value="31"></div>
                            <div><input class="textbox inputform numform" type="number" v-model="spiv" min="0" max="31" step="1" value="31"></div>
                        </div>
                        <div class="col nature" name="nature-">
                            <div><strong>0.9</strong></div>
                            <div></div>
                            <div><input type="radio" class="" v-model="atkratio" value="0.9"></div>
                            <div><input type="radio" class="" v-model="defenratio" value="0.9"></div>
                            <div><input type="radio" class="" v-model="satkratio" value="0.9"></div>
                            <div><input type="radio" class="" v-model="sdefratio" value="0.9"></div>
                            <div><input type="radio" class="" v-model="spratio" value="0.9"></div>
                        </div>
                        <div class="col nature" name="nature+">
                            <div><strong>1.1</strong></div>
                            <div></div>
                            <div><input type="radio" class="" v-model="atkratio" value="1.1"></div>
                            <div><input type="radio" class="" v-model="defenratio" value="1.1"></div>
                            <div><input type="radio" class="" v-model="satkratio" value="1.1"></div>
                            <div><input type="radio" class="" v-model="sdefratio" value="1.1"></div>
                            <div><input type="radio" class="" v-model="spratio" value="1.1"></div>
                        </div>
                        <div class="col nature" name="nature">
                            <div><strong>1.0</strong></div>
                            <div></div>
                            <div><input type="radio" class="" v-model="atkratio" value="1"></div>
                            <div><input type="radio" class="" v-model="defenratio" value="1"></div>
                            <div><input type="radio" class="" v-model="satkratio" value="1"></div>
                            <div><input type="radio" class="" v-model="sdefratio" value="1"></div>
                            <div><input type="radio" class="" v-model="spratio" value="1"></div>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import Header from '../components/header.vue'
    export default {
        components: {Header},
        data() {
            return {
                g_id: 0,
                poke_id: this.$route.params.id,
                next: 0,
                prev: 0,
                pokemon: {type1:{}, ability1:{}, ability2:{}, ability3:{}},
                total: 0,
                ability: 0,
                hp: 0,atk: 0,defen: 0,satk: 0,sdef: 0,sp: 0,
                hpbase:'0', hpex:'0', hpiv:'31',
                atkbase:'0', atkex:'0', atkiv:'31',
                defenbase:'0', defenex:'0', defeniv:'31',
                satkbase:'0', satkex:'0', satkiv:'31',
                sdefbase:'0', sdefex:'0', sdefiv:'31',
                spbase:'0', spex:'0', spiv:'31',
                add: '', sub: '',
                atkratio:'1', defenratio:'1',satkratio:'1',sdefratio:'1',spratio:'1',
            }
        },
        computed: {

            hp_state:function(){
                return parseInt((Number(this.hpiv)+2*this.hp+parseInt(Number(this.hpex)/4))/2)+60
            },
            atk_state:function(){
                return parseInt((parseInt((Number(this.atkiv)+2*this.atk+parseInt(Number(this.atkex)/4))/2)+5)*Number(this.atkratio))
            },
            defen_state:function(){
                return parseInt((parseInt((Number(this.defeniv)+2*this.defen+parseInt(Number(this.defenex)/4))/2)+5)*Number(this.defenratio))
            },
            satk_state:function(){
                return parseInt((parseInt((Number(this.satkiv)+2*this.satk+parseInt(Number(this.satkex)/4))/2)+5)*Number(this.satkratio))
            },
            sdef_state:function(){
                return parseInt((parseInt((Number(this.sdefiv)+2*this.sdef+parseInt(Number(this.sdefex)/4))/2)+5)*Number(this.sdefratio))
            },
            sp_state:function(){
                return parseInt((parseInt((Number(this.spiv)+2*this.sp+parseInt(Number(this.spex)/4))/2)+5)*Number(this.spratio))
            }
        },
        watch: {
            hpbase: function(){if (this.hpbase<253){this.hpex = this.hpbase||0}else{this.hpex =252}},
            hpex: function(){this.hpbase = this.hpex},
            hpiv: function(){if (this.hpiv>31){this.hpiv = 31}else if (this.hpiv<0){this.hpiv = 0}},

            atkbase: function(){if (this.atkbase<253){this.atkex = this.atkbase||0}else{this.atkex =252}},
            atkex: function(){this.atkbase = this.atkex},
            atkiv: function(){if (this.atkiv>31){this.atkiv = 31}else if (this.atkiv<0){this.atkiv = 0}},
            
            defenbase: function(){if (this.defenbase<253){this.defenex = this.defenbase||0}else{this.defenex =252}},
            defenex: function(){this.defenbase = this.defenex},
            defeniv: function(){if (this.defeniv>31){this.defeniv = 31}else if (this.defeniv<0){this.defeniv = 0}},

            satkbase: function(){if (this.satkbase<253){this.satkex = this.satkbase||0}else{this.satkex =252}},
            satkex: function(){this.satkbase = this.satkex},
            satkiv: function(){if (this.satkiv>31){this.satkiv = 31}else if (this.satkiv<0){this.satkiv = 0}},

            sdefbase: function(){if (this.sdefbase<253){this.sdefex = this.sdefbase||0}else{this.sdefex =252}},
            sdefex: function(){this.sdefbase = this.sdefex},
            sdefiv: function(){if (this.sdefiv>31){this.sdefiv = 31}else if (this.sdefiv<0){this.sdefiv = 0}},

            spbase: function(){if (this.spbase<253){this.spex = this.spbase||0}else{this.spex =252}},
            spex: function(){this.spbase = this.spex},
            spiv: function(){if (this.spiv>31){this.spiv = 31}else if (this.spiv<0){this.spiv = 0}},

            '$route' (to, from) {
                // this.$router.go(0);
                this.poke_id = this.$route.params.id
                this.getData()
            }
        },
        created() {
            this.getData()
        },
        methods: {
            getData (){
                this.$api.get('pokemon/' + this.poke_id, {format: 'json'}, r=>{
                    this.pokemon = r;
                    this.g_id = r.category;
                    this.hp = r.hp;this.atk = r.atk;this.defen = r.defen;this.satk = r.satk;this.sdef = r.sdef;this.sp = r.sp;
                    this.ability = r.hp+r.atk+r.defen+r.satk+r.sdef+r.sp;
                    if (this.pokemon.id<923){this.next = this.pokemon.id+1}else {this.next = 1}
                    if (this.pokemon.id>1){this.prev = this.pokemon.id-1}else {this.prev = 923}
                })
            },

        },
        
    }
</script>


