<template>
<div class="container">

    <Header ref="child" v-on:childMethod="getData"></Header> <!--子组件Header调用父组件search方法 -->
    <div class="row clearfix">
		<div class="col-md-6 column">
			<div class="jumbotron" style="height:580px;">
				<h2>
					The Pokemons!
				</h2>
				<p>
					This is a template for a simple marketing or informational website. It includes a large callout called the hero unit and three supporting pieces of content. Use it as a starting point to create something more unique.
				</p>
				<p>
					 <button class="btn btn-primary btn-large" href="#">Learn more</button>
				</p>
			</div>
		</div>
		<div class="col-md-6 column">
			<div class="card">
				<div class="card-header">
					<p class="card-text" style="font-size:12pt">
						第{{g_id}}世代  ：<b>{{firstpoke}}</b> ~ <b>{{lastpoke}}</b>
					</p>
				</div>
				<div class="card-body" style="overflow-y:scroll;height:500px;padding: 3px 3px 3px 3px">
                    
					<table class="table table-hover table-striped table-sm table-responsive" style="font-size:11pt">
                        <thead>
                            <tr>
                                <th>编号</th>
                                <th></th>
                                <th>名字</th>
                                <th>别名</th>
                                <th>属性</th>
                                <th>英文</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="poke in poke_list" :key="poke.id">
                                <td>{{poke.num}}</td>
                                <td><img v-bind:src="'/static/img/icon/'+poke.pic+'.png'" /></td>
                                <td><router-link :to="'/pokemon/'+poke.id">{{poke.name}}</router-link></td>
                                <td>{{poke.name1}}</td>
                                <td>
                                    <span class="badge badge-secondary" v-bind:style="{'background-color':poke.type1.color}">{{poke.type1.name}}</span> <span v-if="poke.type2.name !== '-'" class="badge badge-secondary" v-bind:style="{'background-color':poke.type2.color}">{{poke.type2.name}}</span>
                                </td>
                                <td>{{poke.name_en}}</td>
                            </tr>
                        </tbody>
                    </table>
				</div>
				<div class="card-footer">
					<!-- Panel footer -->
				</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
    import Header from '../components/header.vue'
    export default {
        components: {Header},
        data () {
            return {
                id: this.$route.params.id || 1,
                g_id: 1,
                g_name: '',
                poke_list: [],
                firstpoke: '',
                lastpoke: ''
            }
        },
        watch: { //router-link同页面路由跳转，监听router变化
            '$route' (to, from) {
                // this.$router.go(0);
                this.id = this.$route.params.id || 1
                this.getData()
            }
        },
        computed: {
            // firstpoke:function(){return this.poke_list[0].name},
            // lastpoke:function(){return this.poke_list[this.poke_list.length-1].name},
        },
        created () {
            this.getData()
        },
        methods: {
            getData() {
                var v = this
                v.$api.get('category/'+this.id, {format: 'json'}, function(r){
                    v.g_id = r.id;
                    v.g_name = r.name;
                    v.poke_list = r.cate_poke
                    v.firstpoke = r.cate_poke[0].name
                    v.lastpoke = r.cate_poke[r.cate_poke.length-1].name
                })
            },
        }
    }
</script>


