<template>
<div class="container">
    
    <Header></Header>
    <div class="row clearfix">
		<div class="col-md-5 column">
			<div class="jumbotron" style="height:580px;">
				<h2>
					The Items!
				</h2>
				<p>
					This is a template for a simple marketing or informational website. It includes a large callout called the hero unit and three supporting pieces of content. 
				</p>
				<p>
					 <button class="btn btn-primary btn-large" href="#">Learn more</button>
				</p>
			</div>
		</div>
		<div class="col-md-7 column">
			<div class="card">
				<div class="card-header">
					<h3 class="card-text">
						<!-- 搜索到 {{count}} 个结果 -->
					</h3>
				</div>
				<div class="card-body" style="overflow-y:scroll;height:500px;padding: 3px 3px 3px 3px">
					<table v-if="results" class="table table-hover table-striped  table-sm table-responsive" style="font-size:11pt">
                        <thead>
                            <tr>
                                <th></th>
                                <th>道具</th>
                                <th>英文</th>
                                <th>说明</th>
                            </tr>
                        </thead>
                        <tbody v-if="results">
                            <tr v-for="item in results" :key="item.id">
                                <td><img alt="no pic" v-bind:src="'/static/img/item/'+item.itemid+'.png'" style="width:40px;"/></td>
                                <td>
                                    {{item.name}}
                                    <!-- <router-link :to="'/pokemon/'+poke.id">{{move.name}}</router-link> -->
                                </td>
                                <td>{{item.name_en}}</td>
                                <td>{{item.detail}}</td>
                            </tr>
                        </tbody>
                    </table>
				</div>
				<div class="card-footer">
					<ul class="pagination pagination-sm" style="margin: 0px 0px 0px 0px;">
                        <b-pagination-nav size="sm" :link-gen="linkGen" :number-of-pages="6"/>
                    </ul>
				</div>
			</div>
		</div>
	</div>
</div>
</template>
<script>
    import Header from '../components/header.vue'
    export default {
        components: {Header},
        data () {
            return {
                results: {},
            }
        },
        watch: {
            '$route' (to, from) {
                this.getData()
            }
        },
        computed: {

        },
        created () {
            this.getData()
        },
        methods: {
            getData() {
                var v = this
                v.$api.get('item/', this.$route.query, function(r){
                    v.results = r.results

                })
            },
            linkGen (pageNum) {
                return {
                    path: '/item',
                    query: {page: pageNum}
                }
            },
        }
    }
</script>


