<template>
<div class="container">
    <Header></Header>
    <div>
        
        <!-- Modal Component -->
        <b-modal id="modal1" hide-footer centered no-fade size="lg">
            <div slot="modal-header" class="w-100">
                <b-button-group size="sm" class="float-left">
                     <!-- @click="getpokemon(poke_now, type_now, season_now)" -->
                    <b-button v-for="btn in buttons1" :pressed="btn.id===type_now" :variant="btn.variant" :key="btn.variant" @click="getpokemon(poke_now, btn.id, season_now)">
                        {{ btn.caption }}
                    </b-button>
                </b-button-group>
                <b-button-group size="sm" class="float-right">
                    <b-button v-for="btn in buttons2" :pressed="btn.id===season_now" :variant="btn.variant" :key="btn.variant" @click="getpokemon(poke_now, type_now, btn.id)">
                        {{ btn.caption }}
                    </b-button>
                </b-button-group>
            </div>
            <div class="row">
            <div class="col-md-6">
                <b-card no-body class="mb-1" style="">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-btn block href="#" v-b-toggle.accordion1 variant="info">Moves</b-btn>
                    </b-card-header>
                    <b-collapse id="accordion1" visible accordion="my-accordion" role="tabpanel">
                        <b-card-body style="padding: 0px 0px 0px 0px;">
                            <table v-if="moves" class="table table-hover table-striped table-sm table-responsive" style="font-size:11pt;">
                                <tbody>
                                    <tr v-for="m in moves" :key="m.ranking">
                                        <td style="padding: 3px 3px 3px 3px;width:50px">{{m.ranking}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:250px">{{m.name}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:100px">{{m.usageRate | filter}} %</td>
                                    </tr>
                                </tbody>
                            </table>
                            <p v-else>There is no data to display</p>
                        </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-btn block href="#" v-b-toggle.accordion2 variant="info">Ability</b-btn>
                    </b-card-header>
                    <b-collapse id="accordion2" accordion="my-accordion" role="tabpanel">
                        <b-card-body style="padding: 0px 0px 0px 0px;">
                            <table v-if="ability" class="table table-hover table-striped table-sm table-responsive" style="font-size:11pt;">
                                <tbody>
                                    <tr v-for="m in ability" :key="m.ranking">
                                        <td style="padding: 3px 3px 3px 3px;width:50px">{{m.ranking}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:250px">{{m.name}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:100px">{{m.usageRate | filter}} %</td>
                                    </tr>
                                </tbody>
                            </table>
                            <p v-else>There is no data to display</p>
                        </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-btn block href="#" v-b-toggle.accordion3 variant="info">Nature</b-btn>
                    </b-card-header>
                    <b-collapse id="accordion3" accordion="my-accordion" role="tabpanel">
                        <b-card-body style="padding: 0px 0px 0px 0px;">
                            <table v-if="nature" class="table table-hover table-striped table-sm table-responsive" style="font-size:11pt;">
                                <tbody>
                                    <tr v-for="m in nature" :key="m.ranking">
                                        <td style="padding: 3px 3px 3px 3px;width:50px">{{m.ranking}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:250px">{{m.name}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:100px">{{m.usageRate | filter}} %</td>
                                    </tr>
                                </tbody>
                            </table>
                            <p v-else>There is no data to display</p>
                        </b-card-body>
                    </b-collapse>
                </b-card>
                <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                        <b-btn block href="#" v-b-toggle.accordion4 variant="info">Items</b-btn>
                    </b-card-header>
                    <b-collapse id="accordion4" accordion="my-accordion" role="tabpanel">
                        <b-card-body style="padding: 0px 0px 0px 0px;">
                            <table v-if="items" class="table table-hover table-striped table-sm table-responsive" style="font-size:11pt;">
                                <tbody>
                                    <tr v-for="m in items" :key="m.ranking">
                                        <td style="padding: 3px 3px 3px 3px;width:50px">{{m.ranking}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:250px">{{m.name}}</td>
                                        <td style="padding: 3px 3px 3px 3px;width:100px">{{m.usageRate | filter}} %</td>
                                    </tr>
                                    
                                </tbody>
                            </table>
                            <p v-else>There is no data to display</p>
                        </b-card-body>
                    </b-collapse>
                </b-card>
            </div>
            <div class="col-md-6">
                <div class="row">
                    <div class="col-7">
                        <span class="badge badge-danger" style="position: absolute;left:2;top:0;">{{pokemon.ranking}}</span>
                        <img alt="pgl" height="200" width="200" v-bind:src="'/static/img/pgl/'+pokemon.pokemonId+'.png'" class="img-thumbnail" /></div>
                    <div class="col-5">
                        <span class="badge badge-warning">No.{{pokemon.monsno}}</span>
                        <hr>
                        <b>{{pokemon.name}}</b> 
                        <p style="font-size:12pt">{{pokemon.formName}}</p>
                        <div style="text-align:center">
                            <span class="badge badge-danger" v-if="typeof type1.color !== 'undefined'" v-bind:style="{'background-color':type1.color}">{{type1.name}}</span>
                            <span class="badge badge-danger" v-if="type2 !== null" v-bind:style="{'background-color':type2.color}">{{type2.name}}</span>
                        </div>
                   </div>
                </div>
                <hr>
                <div class="row">
                    <div class="statform">
                        <div class="col labelcol" style="width:70px">
                            <div><b>Hp</b></div>
                            <div><b>攻击</b></div>
                            <div><b>防御</b></div>
                            <div><b>特攻</b></div>
                            <div><b>特防</b></div>
                            <div><b>速度</b></div>
                        </div>
                        <div class="col basestatscol" style="width:30px">
                            <div><b>{{pokemodal.hp}}</b></div>
                            <div><b>{{pokemodal.atk}}</b></div>
                            <div><b>{{pokemodal.defen}}</b></div>
                            <div><b>{{pokemodal.satk}}</b></div>
                            <div><b>{{pokemodal.sdef}}</b></div>
                            <div><b>{{pokemodal.sp}}</b></div>
                        </div>
                        <div class="col graphcol" style="width:100px">
                            <div v-if="pokemodal.id === 335"><em><span style="background:rgb(100,100,0);width:1px"></span></em></div>
                            <div v-if="pokemodal.id !== 335"><em><span v-bind:style="{background:'rgb(100,'+pokemodal.hp+',0)',width:pokemodal.hp+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+pokemodal.atk+',0)',width:pokemodal.atk+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+pokemodal.defen+',0)',width:pokemodal.defen+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+pokemodal.satk+',0)',width:pokemodal.satk+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+pokemodal.sdef+',0)',width:pokemodal.sdef+'px'}"></span></em></div>
                            <div><em><span v-bind:style="{background:'rgb(100,'+pokemodal.sp+',0)',width:pokemodal.sp+'px'}"></span></em></div>
                        </div>
                    </div>
                </div>
                <hr>
                <div class="row">
                    <v-select label="name" :options="allpoke" maxHeight="150px" style="font-size:12pt"></v-select>
                </div>
            </div>
            </div>
        </b-modal>
    </div>
	<b-card no-body>
        <div class="" style="text-align: center">
            <span class="label label-danger"><b>Season {{season}} </b>--{{updateDate}}--</span>
        </div>
        <b-tabs card style="font-size:12pt">
            <b-tab title="Single" active>
                <div class="img-wrap" v-for="each in s1" :key="each.rank">
                    <div class="img-wrap1"><p><span class="badge badge-danger">{{each.rank}}</span>&nbsp;&nbsp;<b>{{each.name}}</b></p></div>
                    <div class="img-wrap2">
                        <a href="" v-b-modal.modal1 @click.prevent="getpokemon(each.id, '1', season)">
                            <img v-bind:src="'/static/img/pgl/'+each.id+'.png'" class="img-thumbnail">
                        </a>
                    </div>
                </div>
            </b-tab>
            <b-tab title="Double">
                <div class="img-wrap" v-for="each in s2" :key="each.rank">
                    <div class="img-wrap1"><p><span class="badge badge-danger">{{each.rank}}</span>&nbsp;&nbsp;<b>{{each.name}}</b></p></div>
                    <div class="img-wrap2">
                        <a href="" v-b-modal.modal1 @click.prevent="getpokemon(each.id, '2', season)">
                            <img v-bind:src="'/static/img/pgl/'+each.id+'.png'" class="img-thumbnail">
                        </a>
                    </div>
                </div>
            </b-tab>
            <b-tab title="Special">
                <div class="img-wrap" v-for="each in s5" :key="each.rank">
                    <div class="img-wrap1"><p><span class="badge badge-danger">{{each.rank}}</span>&nbsp;&nbsp;<b>{{each.name}}</b></p></div>
                    <div class="img-wrap2">
                        <a href="" v-b-modal.modal1 @click.prevent="getpokemon(each.id, '5', season)">
                            <img v-bind:src="'/static/img/pgl/'+each.id+'.png'" class="img-thumbnail">
                        </a>
                    </div>
                </div>
            </b-tab>
            <b-tab title="Wcs">
                <div class="img-wrap" v-for="each in s6" :key="each.rank">
                    <div class="img-wrap1"><p><span class="badge badge-danger">{{each.rank}}</span>&nbsp;&nbsp;<b>{{each.name}}</b></p></div>
                    <div class="img-wrap2">
                        <a href="" v-b-modal.modal1 @click.prevent="getpokemon(each.id, '6', season)">
                            <img v-bind:src="'/static/img/pgl/'+each.id+'.png'" class="img-thumbnail">
                        </a>
                    </div>
                </div>
            </b-tab>
        </b-tabs>
    </b-card>

</div>
</template>
<script>
    
    import Header from '../components/header.vue'
    export default {
        components: {Header},
        data () {
            return {
                season: this.$route.params.season || '310',
                s1: {},s2: {},s5: {},s6: {},
                updateDate: '',
                s: ['Season 310', 'Season 309', 'Season 308'],
                items: {},moves: {},nature: {},ability: {},
                pokemon: {},
                pokemodal: {},
                type1: {}, type2: {},
                allpoke: {},
                buttons1: [
                    { variant: 'outline-success', caption: 'Single', state: false, id: '1'},
                    { variant: 'outline-warning', caption: 'Double', state: false, id: '2'},
                    { variant: 'outline-info', caption: 'Special', state: false, id: '5'},
                    { variant: 'outline-danger', caption: 'Wcs', state: false, id: '6'},
                ],
                buttons2: [
                    { variant: 'outline-primary', caption: 'Season308', state: false, id: '308'},
                    { variant: 'outline-info', caption: 'Season309', state: false, id: '309'},
                    { variant: 'outline-success', caption: 'Season310', state: false, id: '310'},
                ],
                poke_now: '',
                type_now: '',
                season_now: this.$route.params.season || '310',
            }
        },
        watch: {
            '$route' (to, from) {
                this.season = this.$route.params.season || '310'
                this.season_now = this.$route.params.season || '310'
                this.getData()
            }
        },
        computed: {

        },
        created () {
            this.getData()
        },
        filters: {
            filter(v){
                return Number(v).toFixed(2)
            }
        },
        methods: {
            getData() {
                var v = this
                v.$api.get('pgl/'+this.season, {format: 'json'} , function(r){
                    v.s1 = r.s1
                    v.s2 = r.s2
                    v.s5 = r.s5
                    v.s6 = r.s6
                    v.updateDate = r.updateDate
                    v.season = v.$route.params.season || '310'
                })
            },
            getpokemon(a, b, c){
                var v = this
                v.poke_now = a
                v.type_now = b
                v.season_now = c
                v.$api.post('pgl/', {poke_id:a, type_id:b, season_id:c}, function(r){
                    if (r.trend !== ''){
                        if (r.trend.itemInfo){v.items = r.trend.itemInfo.slice(0,10)}else{v.items=false}
                        v.moves = r.trend.wazaInfo.slice(0,10)
                        v.nature = r.trend.seikakuInfo.slice(0,10)
                        v.ability = r.trend.tokuseiInfo.slice(0,10)
                    } else {v.items=false, v.moves=false, v.nature=false, v.ability=false}
                    v.pokemon = r.poke
                    v.pokemodal = r.pokemodal
                    v.type1 = r.type1
                    v.type2 = r.type2
                    v.allpoke = r.allpoke
                })
            },
            selectpoke(a){
                console.log(a)

            },
        }
    }
</script>


